.cases {
  width: 100%;
  padding-left: 150px;
  padding-right: 150px;
  padding-bottom: 150px;
  padding-top: 50px;
  box-sizing: border-box;
  
}


@media screen and (max-width: 1600px) {
    .cases {
        padding-right: 50px;
        padding-bottom: 100px;
    }
  }

  @media screen and (max-width: 1300px) {
    .cases {
        padding-left: 100px;
    }
  }

  @media screen and (max-width: 900px) {
    .cases {
        padding-left: 60px;
    }
  }

.cases__title {
    color: black;
    font-size: 60px;
    margin: 0;
    font-weight: 700;
    padding-bottom: 50px;
}

@media screen and (max-width: 1600px) {
    .cases__title {
      font-size: 48px;
      padding-bottom: 30px;
    }
  }
  

.cases__subtitle {
    font-size: 24px;
    line-height: 34px;
    margin: 0;
    color: black;
    font-weight: 400;
    max-width: 900px;
    
}


@media screen and (max-width: 1600px) {
    .cases__subtitle {
      font-size: 18px;
      line-height: 24px;
      max-width: 700px;
    }
  }

.case_wrapper {
    position: relative;
    width: 100%; 
    min-height: 450px; 
    display: flex; 
    align-items: flex-end;
    
}

@media screen and (max-width: 1600px) {
    .case_wrapper {
        min-height: 370px; 
    }
  }

  @media screen and (max-width: 900px) {
    .case_wrapper {
        min-height: 280px; 
    }
  }

.case {
    position: absolute; 
    top: 50px;
    left: -50px;
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: left;
    width: 100%;
    transition: opacity 2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}


@media screen and (max-width: 900px) {
  .case {
    left: -60px; 
   
  }
}


.case_active {
    opacity: 1;
    
}

.case__text-wrapper {
    min-width: 90%;
     background: linear-gradient(315deg, rgb(5, 36, 82) 39%, rgb(3, 85, 137) 95%); 
    height: 400px; 
       
}

@media screen and (max-width: 1600px) {
    .case__text-wrapper {
        height: 310px; 
    }
  }

  @media screen and (max-width: 900px) {
    .case__text-wrapper {
        height: 420px; 
     
    }
  }

.case__text {
    max-width: 800px;
    color: white;
    font-size: 40px;
    line-height: 48px;
    padding-left: 50px;
    font-weight: 700;
    margin:0;
    padding: 30px 50px;
  
}

@media screen and (max-width: 1600px) {
    .case__text {
        font-size: 30px;
        line-height: 38px; 
        max-width: 500px;
    }
  }

  @media screen and (max-width: 1100px) {
    .case__text {
        font-size: 24px;
        line-height: 30px; 
        max-width: 580px;
    }
  }

  
  @media screen and (max-width: 900px) {
    .case__text {
      padding: 30px 30px 0 60px;
      max-width: 700px;
    }
  }



.case__image {
    width: 920px;
    position: absolute;
    right: -150px;
    bottom: -40px;
    object-fit: cover;
    
}

@media screen and (max-width: 1600px) {
    .case__image {
        width: 650px;
        right: -70px;
        bottom: -20px;
    }
  }


  @media screen and (max-width: 1200px) {
    .case__image {
        width: 500px;
        right: -70px;
        bottom: -20px;
    }
  }

  @media screen and (max-width: 900px) {
    .case__image {
        width: 500px;
        right: -100px;
        bottom: -20px;
    }
  }

.case__button {
    width: 150px;
    height: 50px;
    border: none;
    outline: none;
    justify-self: end;
    z-index: 5;
    margin: 0;
    margin-bottom: 34px;  
    background-color: transparent;
    color: white;
    font-size: 16px;
    border-radius: 25px;
    font-weight: 700;
    border: 2px solid #12abdb;
    transition: all .4s ease-in-out;
}

@media screen and (max-width: 1600px) {
    .case__button {
      transform: scale(0.8);
      margin-bottom: 20px;  
    }
  }


.case__button:hover {
    cursor: pointer;
    
}

.case__button_next { 
  width: 90px;
  padding: 0;
  border: none;
  margin-left: 200px;
}


@media screen and (max-width: 1600px) {
    .case__button_next {
        margin-left: 150px;;   
    }
  }

.case__button_about{
   position: absolute;
   left: 50px;
   bottom: 0px;    
}

@media screen and (max-width: 1600px) {
    .case__button_about {
        bottom: -10px; 
        left: 30px;
    }
  }

  @media screen and (max-width: 900px) {
    .case__button_about {
        bottom: 190px; 
        left: 30px;
    }
  }


.case__button_about:hover {
    background-color: #12abdb;
    
}

.case__button_next:hover {
    color: #12abdb;
    
}

.case__button_projects {
    color:#12abdb;
    margin-top: 20px;   
    width: 300px;   
}

@media screen and (max-width: 1600px) {
    .case__button_projects {
        margin-top: 0px; 
        width: 250px;   
    }
  }

  @media screen and (max-width: 900px) {
    .case__button_projects {
        margin-top: 0px; 
        width: 250px; 
        color: #FFF;  
    }
  }

.case__button_projects:hover {
    background-color: #12abdb;
    color: white;
}



.next_ico {
    color: #fff; 
    width: 25px;
    height: 25px;
    margin-right: 4px;
    z-index: 5;
    
    vertical-align: middle;
  
}